@charset "UTF-8";
/*--------------------------------------------------------------
## Variables & Mixins
--------------------------------------------------------------*/
/* PX to REM font-size mixin */
/**
 * Aspect ratio with fallback
 */
/*--------------------------------------------------------------
## Normalize
--------------------------------------------------------------*/
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #404040;
  font-family: "Montserrat" sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

@font-face {
  font-family: "Heritage";
  src: url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.eot);
  src: local("Heritage"), url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.eot?#iefix) format("embedded-opentype"), url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.woff2) format("woff2"), url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.woff) format("woff"), url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.ttf) format("truetype"), url(/wp-content/themes/KayMozCeramics/public/assets/fonts/Heritage.svg#Heritage) format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  font-family: Montserrat, sans-serif;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #000;
  word-wrap: break-word;
}

h1 {
  font-size: 1.6rem;
}

h2 {
  font-size: 1.454rem;
}

h3 {
  font-size: 1.331rem;
}

h4 {
  font-size: 1.21rem;
}

h5 {
  font-size: 1.1rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1rem;
  margin-bottom: 1.5rem;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5rem;
}

address {
  margin: 0 0 1.5rem;
}

pre {
  background: #eeeeee;
  font-family: "Courier 10 Pitch" Courier monospace;
  font-size: 0.9375;
  /* add PX (or original value) as backup */
  /* do nothing, "inherit" has no unit */
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem;
}

code,
kbd,
tt,
var {
  font-family: Monaco Consolas "Andale Mono" "DejaVu Sans Mono" monospace;
  font-size: 1rem;
}

abbr,
acronym {
  border-bottom: 1px dotted #666666;
  cursor: help;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*--------------------------------------------------------------
## Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: white;
}

blockquote,
q {
  quotes: "" "";
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: "";
}

hr {
  background-color: #cccccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul,
ol {
  margin: 0 0 1.5em 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  height: auto;
  max-width: 100%;
}

table {
  margin: 0 0 1.5em;
  width: 100%;
}

/*--------------------------------------------------------------
## Forms
--------------------------------------------------------------*/
button,
button[type=submit],
input[type=button],
input[type=reset],
input[type=submit],
.button.wc-backward,
.button,
a.button,
.wp-block-button > a {
  display: inline-block;
  border: none;
  border-radius: 3px;
  background: #52a4a0;
  box-shadow: none;
  color: #fff;
  font-size: 18px;
  line-height: 1;
  padding: 1.1em 2em;
  width: 100%;
  text-align: center;
}
@media screen and (min-width: 915px) {
  button,
button[type=submit],
input[type=button],
input[type=reset],
input[type=submit],
.button.wc-backward,
.button,
a.button,
.wp-block-button > a {
    font-size: 15px;
    width: unset;
  }
}
button:hover, button:focus, button:active,
button[type=submit]:hover,
button[type=submit]:focus,
button[type=submit]:active,
input[type=button]:hover,
input[type=button]:focus,
input[type=button]:active,
input[type=reset]:hover,
input[type=reset]:focus,
input[type=reset]:active,
input[type=submit]:hover,
